:root {
  --blue-color: #00acdc;
  --black-color: #000;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  display: flex;
}

.link_image_logo {
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  list-style: none;
}

.nav__item:first-child {
  margin: 0;
}

.nav__link {
  text-transform: uppercase;
  color: var(--black-color);
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.nav__link:hover {
  color: var(--blue-color);
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}
